.single_pricing.active {
  --tw-border-opacity: 1;
  border-color: rgba(249, 79, 79, var(--tw-border-opacity));
  --tw-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  box-shadow: 0 0 #0000, 0 0 #0000, var(--tw-shadow);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.pricing_title .title {
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.75rem;
  position: absolute;
  top: 50%;
  left: 50%;
  --tw-text-opacity: 1;
  color: rgba(56, 66, 77, var(--tw-text-opacity));
  --tw-translate-x: 0;
  --tw-translate-y: 0;
  --tw-rotate: 0;
  --tw-skew-x: 0;
  --tw-skew-y: 0;
  --tw-scale-x: 1;
  --tw-scale-y: 1;
  -webkit-transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
          transform: translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  --tw-translate-x: -50%;
  --tw-translate-y: -50%;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  transition-duration: 300ms;
}

.single_pricing.active .pricing_title .title {
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.single_pricing.active .services_shape, .single_pricing:hover .services_shape{
  fill: currentColor;
  --tw-text-opacity: 1;
  color: rgba(249, 79, 79, var(--tw-text-opacity));
}

.pricing_btn {
  border-radius: 0.375rem;
  margin-top: 32px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 24px;
  padding-right: 24px;
  --tw-text-opacity: 1;
  color: rgba(116, 126, 136, var(--tw-text-opacity));
  background-color: #fff1f1;
}

.single_pricing:hover .pricing_btn, .single_pricing.active .pricing_btn{
  --tw-bg-opacity: 1;
  background-color: rgba(249, 79, 79, var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

